import { Button, Modal } from 'antd'
import React, { useState } from 'react'
import ContactUsModal from '../../components/ContactUsModal'
import Footer from '../../components/Footer'
import PageHeader from '../../components/Header'
import MarketingCloud from '../../components/Services/MarketingCloud'

export default () => {
  const [isModalOpen, setIsModalOpen] = useState(false);

  const showModal = () => {
    setIsModalOpen(true);
  };

  const handleOk = () => {
    setIsModalOpen(false);
  };

  const handleCancel = () => {
    setIsModalOpen(false);
  };
  const activeKey = location.hash ? location.hash.substring(1) : "";
  return (
    <div>
      <ContactUsModal isModalOpen={isModalOpen} handleOk={handleOk} handleCancel={handleCancel}/>
      <PageHeader selectedKey="services" isTransparentHeader={false} />
      <MarketingCloud showContactUs={showModal} activeKey={activeKey} />
      <Footer />
    </div>
  )
}
